*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input {
  width: 99%;
  font-size: 1.2rem;
  color:grey; 
  text-align: center;
}

.card-grid {
  display:flex;
  flex-direction: row;
  flex-wrap:wrap;
}

.card {
 align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 25px;
  margin-right:25px;
}

.card p {
  padding: 5px;
  text-align:center;
}

.card img {
  max-height: 170px;
 
}

.back{
  height:100%;
  background:black;
  color:white;
  padding: 50px;
  margin-bottom: 30px;
}

.titleOne{
    text-align: center;
    
}

.title, .load{
  text-align: center;
  padding-top:20px ;
}
